:root {
  --grid-base: 8px;
}
.root {
	width: 100vw;
	height: 300px;
	max-height: 300px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	overflow: hidden;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	position: relative;
	left: 50%;
	right: 50%;
	margin: 0 -50vw 2rem;
}
.image {
	width: 100vw;
	height: 300px;
	-o-object-fit: cover;
	   object-fit: cover;
}
.description {
	margin-left: calc(5 * 8px);
	margin-left: calc(5 * var(--grid-base));
	margin-top: calc(8px);
	margin-top: calc(var(--grid-base));
	position: absolute;
	font-size: 0.8rem;
	top: 0;
	color: #fff;
}
