.root {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}

.title {
	margin-top: 0;
}

.email {
	margin: 90px 0px 40px;
	font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, sans-serif, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
		'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.form {
	background-color: rgb(255, 255, 255);
	border-radius: 6px;
	-webkit-box-shadow: 0 2px 15px 0 rgba(210, 214, 220, 0.5);
	        box-shadow: 0 2px 15px 0 rgba(210, 214, 220, 0.5);
	max-width: 700px;
	overflow: hidden;
}

.gridcontainer {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
	width: 100%;
}

.column {
	padding: 20px;
}

.gdpr {
	background-color: #f3f3f3;
}

.gdprcheck {
	display: block;
	padding-left: 15px;
	text-indent: -15px;
}

.checkbox {
	width: 13px;
	height: 13px;
	padding: 0;
	margin: 2px;
	vertical-align: bottom;
	position: relative;
	top: -1px;
	*overflow: hidden;
}

.info {
	margin: 15px 0;
	font-size: 0.7rem;
}

.input {
	width: 100%;
	border-color: rgb(227, 227, 227);
	border-radius: 4px;
	color: rgb(0, 0, 0);
	font-weight: 400;
	background: #fff;
	font-size: 15px;
	padding: 12px;
	border: 1px solid #e3e3e3;
	-webkit-box-flex: 1;
	    -ms-flex: 1 0 auto;
	        flex: 1 0 auto;
	line-height: 1.4;
	margin: 5px;
	-webkit-transition: border-color 0.3s ease-out;
	transition: border-color 0.3s ease-out;
}

.fieldset {
	border: 0;
}

.link {
	color: #003866;
}

.submit {
	color: #003866;
	text-decoration: none;
	background: #ffffff;
	padding: 10px;
	border: 2px solid #494949;
	display: inline-block;
	-webkit-transition: all 0.4s ease 0s;
	transition: all 0.4s ease 0s;
	cursor: pointer;
}

.submit:hover {
	color: #ffffff;
	background: #003866;
	border-color: #003866;
	-webkit-transition: all 0.4s ease 0s;
	transition: all 0.4s ease 0s;
}
